<template>
	<div class="box">
	      <van-nav-bar
            left-text=""
            left-arrow
            @click-left="onClickLeft"
            />
         <div class="demo">
             <video-player class="video-player vjs-custom-skin"
                      ref="videoPlayer"
                      :playsinline="true"
                      :options="playerOptions">
             </video-player>
         </div>  
         <div class="ismv" v-show="ismv">
               <div  v-if="num.liked">
                     <p><van-icon name="like" color='red'/></p>
               </div>
               <div v-else>
                    <p><van-icon name="like" color='green'/></p>
               </div>
               <p><van-icon name="thumb-circle" />{{num.commentCount}}</p>
               <p><van-icon name="star" />{{num.likedCount}}</p>
               <p><van-icon name="share" />{{num.shareCount}}</p>
         </div> 
	</div>
</template>

<script>
import __Config from '../api/config.js';//api
import Vue from 'vue';
import { Icon } from 'vant';
Vue.use(Icon);
import { NavBar } from 'vant';
Vue.use(NavBar);
	export default {
        name:"Transtion_detail",
		components: {
			
		},
	
		data() {
			return {
                  ismv:false,//MV
                  num:{},
                  /*---------------------------------- */
                    playerOptions: {
                        //
                        playbackRates: [0.5, 1.0, 1.5, 2.0], 
                        //true,
                        autoplay: true, 
                        // 
                        muted: false, 
                        // 
                        loop: false, 
                        // <video>auto,
                        preload: 'auto', 
                        language: 'zh-CN',
                        //  - "16:9""4:3"
                        aspectRatio: '16:9',
                        // trueVideo.js player
                        fluid: true,
                        sources: [{
                            //
                            type: "video/mp4",
                            //url
                            src: '' 
                        }],
                        //
                        poster: '', 
                        //Video.js
                        notSupportedMessage: '',
                        controlBar: {
                            timeDivider: true,
                            durationDisplay: true,
                            remainingTimeDisplay: false,
                            //
                            fullscreenToggle: true  
                        }
                    }

                 /*---------------------------------- */
			}
		},
		methods:{
			  onClickLeft() {
                   window.history.back(-1);
               },	
                
              //
              getmVustart(id){
                   var that =this;
                    this.$axios({
                        method: 'get',
                        dataType: 'json',
                        url:__Config.api.getmVustart+id,
                    }).then(res => {
                   
                       that.num =res.data;
                    }).then(err =>{

                    }).catch(function (err) {
                        
                    });		
              }	
        },
		created:function(){
              this.playerOptions.poster =  this.$route.query.img;
              let type = this.$route.query.type;
              let url;
              //Mv
               if(type==2){
                     url = __Config.api.getmVurl+this.$route.query.id;
               }else if(type==3){
                   //
                     url = __Config.api.getMoveUrl +this.$route.query.id;
               }
              var that =this;
                this.$axios({
                    method: 'get',
                    dataType: 'json',
                    url:url,
                }).then(res => {
                    if (type==2) {
                        //MV 
                           that.ismv =true;
                           var moveyrl = [{
                        // MV
                            type: "video/mp4",
                            //url
                            src:res.data.data.url
                         }];
                         that.playerOptions.sources= moveyrl;
                         that.getmVustart(this.$route.query.id);
                    } else if(type==3){
                         var moveyrl = [{
                        //
                            type: "video/mp4",
                            //url
                            src:res.data.urls[0].url
                         }];
                         that.playerOptions.sources= moveyrl;
                      
                    }
                  
                }).then(err =>{

                }).catch(function (err) {
                    
                });	
             
		}
	}
</script>
<style scoped>
.box{
    width: 100%;
    height: 100%;
    background: #000;
}
.demo{
    width: 100%;
    height: 338px;
  
  }
  .demo:hover{
    display: block;
  }
  .ismv{
     color: #fff;
    width: 95%;
    height: 100px;
    background: #ffffff1c;
    margin: -90px auto;
  }
  .ismv p{
      margin-top: 3px;;
  }
</style>



