import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Home from '@/components/Home'
import Transition from '@/components/Transition'
import Notfound from '@/components/Notfound'
import Login from '@/components/login/Login' //
import Daymusic from '@/components/home/Daymusic' //
import Playmusic from '@/components/home/Playmusic' //
import Transtion_detail from '@/components/Transtion_detail' //
import Singer from '@/components/singer/Singer' //
import SingerPlay from '@/components/singer/SingerPlay' //50
import Radio from '@/components/home/Radio' //
import Radio_detail from '@/components/home/Radio_detail' //
import RadioType from '@/components/RadioType' //
import HotRadio from '@/components/home/HotRadio' //
import SerchMusic from '@/components/serchMusic/SerchMusic' //
import User from '@/components/user/User' //
import Video from '@/components/home/Video' //
import PayBoutique from '@/components/home/PayBoutique' // 
import TypeRadio from '@/components/home/TypeRadio' //
import Nearsong from '@/components/user/nearsong' //
import PlayVideo from '@/components/home/PlayVideo' //mv


Vue.use(Router)

const router = new Router({
	//mode: 'history', //url#  
	routes: [{ ///
		  path: '/',
	        	redirect: '/login'
			},
	         {
				path: '/login',
				name: 'Login',
				component: Login,
				meta: {
					title: '',
					index: 1,
					requireAuth: false
				}
			},
		{ //
			path: '/index',
			name: 'Index',
			component: Index,
			meta: {
				title: '',
				index: 2,
				requireAuth: false
			}
		},
	
		{ //
			path: '/home',
			name: 'Home',
			component: Home,
			meta: {
				title: '',
				index:3,
				requireAuth: true
			}
		},
		{//
			path: '/daymusic',
			name: 'Daymusic',
			component: Daymusic,
			meta: {
				title: '',
				index: 4,
				requireAuth: true
			}
		},
		{//
			path: '/playmusic',
			name: 'Playmusic',
			component: Playmusic,
			meta: {
				title: '',
				index:5,
				requireAuth: true
			}
		},
		
		{//
			path: '/transtion_detail',
			name: 'transtion_detail',
			component: Transtion_detail,
			meta: {
				title: '',
				index:6,
				requireAuth: true
			}
		},
		{//
			path: '/singer',
			name: 'Singer',
			component: Singer,
			meta: {
				title: '',
				index:7,
				requireAuth: true
			}
		},
		{//50
			path: '/singerPlay',
			name: 'SingerPlay',
			component: SingerPlay,
			meta: {
				title: '50',
				index:8,
				requireAuth: true
			}
		},
		{ //
			path: '/transition',
			name: 'transition',
			component: Transition,
			meta: {
				title: '',
				index: 9,
				requireAuth: true
			}
		},
		{ //
			path: '/radio',
			name: 'radio',
			component: Radio,
			meta: {
				title: '',
				index: 10,
				requireAuth: true
			}
		},
		{ //
			path: '/radio_detail',
			name: 'Radio_detail',
			component: Radio_detail,
			meta: {
				title: '',
				index: 11,
				requireAuth: true
			}
		},
		{ //
			path: '/radioType',
			name: 'RadioType',
			component: RadioType,
			meta: {
				title: '',
				index: 12,
				requireAuth: true
			}
		},
		{ //
			path: '/hotRadio',
			name: 'HotRadio',
			component: HotRadio,
			meta: {
				title: '',
				index: 13,
				requireAuth: true
			}
		},
		{ //
			path: '/serchMusic',
			name: 'serchMusic',
			component: SerchMusic,
			meta: {
				title: '',
				index: 14,
				requireAuth: true
			}
		},
		{ //
			path: '/user',
			name: 'user',
			component: User,
			meta: {
				title: '',
				index: 15,
				requireAuth: true
			}
		},
		{ //
			path: '/video',
			name: 'video',
			component:Video,
			meta: {
				title: '',
				index: 16,
				requireAuth: true
			}
		},
		{ // 
			path: '/payBoutique',
			name: 'payBoutique',
			component:PayBoutique,
			meta: {
				title: '',
				index: 17,
				requireAuth: true,
			}
		},
		{ // 
			path: '/typeRadio',
			name: 'typeRadio',
			component:TypeRadio,
			meta: {
				title: '',
				index: 18,
				requireAuth: true,
			}
		},
		{ // 
			path: '/nearsong',
			name: 'nearsong',
			component:Nearsong,
			meta: {
				title: '',
				index: 19,
				requireAuth: true,
			}
		},
		{ // mv
			path: '/PlayVideo',
			name: 'PlayVideo',
			component:PlayVideo,
			meta: {
				title: 'mv',
				index:20,
				requireAuth: true,
			}
		},

      ///////////////////////////////////////////////////////////////////
	
		{
			path: '/404',
			name: '404',
			component: Notfound
		},
		{
			path: '*',//
			redirect: '/404' //404
		}
	]
})

export default router;

